import React from "react";
import { Grid, CircularProgress, Typography, Hidden, Divider, Theme } from "@material-ui/core";
import RankingCard, { RankProps } from "./RankingCard";
import API from "../../Services/API";
import LineCard from "./LineCard"
import ProfitCard from "./ProfitCard";
import PieCard from "./PieCard";
import WithTimeTravel from "./WithTimeTravel";
import { ROLES } from "../../Globals";
import CardWithCycleTravel from "./lib/CardWithCycleTravel";
import RestrictiveGrid from "./lib/RestrictiveGrid";
import HierarchyFeed from "./HierarchyFeed";
import { withStyles } from "@material-ui/styles";
import HierarchyGraph, { ExplanationContainer } from "./HierarchyFeed/HierarchyGraph";

const TimeTravelRank = WithTimeTravel<RankProps>(RankingCard);
const PersonalGraph = WithTimeTravel(LineCard);
const TimeTravelHierarchyFeed = CardWithCycleTravel(HierarchyFeed);
const TimeTravelHierarchyGraph = CardWithCycleTravel(props => (<>
    <HierarchyGraph data={props.data.descendants} />
    <ExplanationContainer usuarios={props.data.descendants || []} />
</>));


function stylesCreator(theme: Theme) {
    return {
        root: {
            margin: "auto",
            width: `calc(100% - ${(theme.spacing(2) * 2)}px)`,
            textAlign: "center",
            overflow: "hidden"
        }
    };
}

export default withStyles(stylesCreator as any)(class Feed
    extends React.Component<{ onMounted: Function, username: string, rol: number } & { classes: any }> {
    unmounted = false;
    state = {
        loading: true
        , error: null
        , week_ranking: []
        , group_graph: []
        , personal_graph: []
        , year_sales: []
        , product_resume: []
        , group_ranking: []
        , descendants: []
        , descendants_resume: null
        , current_week: null
        , current_cycle: null
        , profit_summary: null
    }
    constructor(props) {
        super(props);
        props.onMounted(props.match.path);
    }
    componentDidMount() {
        const stats = [
            "week_ranking",
            "personal_graph",
            "year_sales",
            "product_resume",
            "current_week",
            "descendants",
            "current_cycle",
            "profit_summary"
        ] as Array<keyof DashboardResult>;
        if (!this.esAdmin()) stats.push(
            "descendants_resume"
        );
        //if (this.props.rol === ROLES.ADMINISTRADOR) stats.push("group_graph");
        // if (this.props.rol === ROLES.ADMINISTRADOR) stats.push("group_ranking");
        API.Stats({ stats })
            .then(x => {
                if (this.unmounted) return;
                this.setState({ ...x, loading: null })
            })
            .catch(error => {
                if (this.unmounted) return;
                this.setState({ error, loading: null });
            });
    }
    componentWillUnmount = () => this.unmounted = true;
    productResumeReducer = data => data.map(a => {
        a.username = a.label;
        return a;
    });
    esAdmin = () => {
        return this.props.rol === ROLES.ADMINISTRADOR;
    }
    render() {
        var {
            loading, error,
            week_ranking,
            personal_graph,
            group_graph,
            year_sales,
            product_resume,
            group_ranking,
            current_week,
            descendants,
            descendants_resume,
            current_cycle,
            profit_summary
        } = this.state;
        const { rol, username } = this.props;
        var resume = { month: 0, year: 0 };
        var ahora = new Date();
        const esAdmin = this.esAdmin();

        for (let i = 0; i < year_sales.length; i++) {
            const x = year_sales[i];
            x.createdAt = new Date(x.createdAt);
            if (x.createdAt.getMonth() === ahora.getMonth() &&
                x.createdAt.getFullYear() === ahora.getFullYear()) {
                resume.month += x.profit;
            }
            resume.year += x.profit;
        }

        if (loading) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <CircularProgress size={100} />
            <Typography variant="h6">CARGANDO...</Typography>
        </div>);

        if (error) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <Typography variant="h5">OCURRI UN PROBLEMA</Typography>
            <Typography variant="subtitle1">{error.message}</Typography>
            <Typography variant="body1">{error.status} {error.code}</Typography>
        </div>);

        return (<Grid container spacing={2} className={this.props.classes.root} >
            <Grid item xs={12}>
                <Typography color="primary" variant="h6">
                    Hola {username}. Esta es tu oficina virtual.
                </Typography>
                <Typography variant="subtitle1">
                    Estamos aqu para prosperar juntos.
                </Typography>
            </Grid>

            <RestrictiveGrid show={group_ranking && group_ranking.length > 0}>
                <TimeTravelRank
                    statName="group_ranking"
                    data={group_ranking}
                    current_week={current_week}
                    title="RANKING DE GRUPOS"
                    colType="MONEY"
                    secondColType="MONEY"
                    col1="stat"
                    col2="ventas_lider"
                />
            </RestrictiveGrid>

            <Grid item sm={6} xs={12}>
                <PersonalGraph
                    data={personal_graph}
                    statName="personal_graph"
                    current_week={current_week}
                    title={"ESTADISTICA SEMANAL"}
                />
            </Grid>

            <RestrictiveGrid sm={6} show={(group_graph && group_graph.length > 0)}>
                <PieCard data={product_resume} title="PRODUCTOS VENDIDOS" />
            </RestrictiveGrid>

            <RestrictiveGrid sm={6} show={!esAdmin && !!Object.values(profit_summary || {}).filter(x => x).length}>
                <ProfitCard {...(profit_summary || {})} />
            </RestrictiveGrid>

            <Hidden smUp>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
            </Hidden>

            <RestrictiveGrid sm={6} show={esAdmin} >
                <TimeTravelRank
                    current_week={current_week}
                    statName="product_resume"
                    reduceFn={this.productResumeReducer}
                    data={product_resume}
                    title="PRODUCTOS VENDIDOS"
                    colType="NUMBER"
                    col1="value"
                />
            </RestrictiveGrid>

            <RestrictiveGrid sm={6} show={esAdmin} >
                <TimeTravelRank
                    statName="week_ranking"
                    current_week={current_week}
                    data={week_ranking}
                    reduceFn={data => esAdmin ? data.filter(x => x.rol === ROLES.ADMINISTRADOR) : data}
                    title="RANKING DE LA SEMANA"
                    colType="MONEY"
                    secondColType="DATE"
                    col1="stat"
                    col2="ultimoPedido"
                />
            </RestrictiveGrid>

            <RestrictiveGrid sm={6} show={esAdmin} >
                <RankingCard
                    data={week_ranking}
                    title="RANKING ACUMULATIVO"
                    colType="MONEY"
                    col1="monto_acumulativo"
                />
            </RestrictiveGrid>

            <Hidden smUp>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
            </Hidden>

            <RestrictiveGrid show={!esAdmin && descendants_resume && descendants.length > 1}>
                <TimeTravelHierarchyFeed
                    stats={["descendants", "descendants_resume"]}
                    data={{
                        descendants_resume,
                        descendants
                    }}
                    current_cycle={current_cycle}
                    title="Genealoga"
                />
            </RestrictiveGrid>

            <RestrictiveGrid show={esAdmin && descendants.length > 1}>
                <TimeTravelHierarchyGraph
                    stats={["descendants"]}
                    data={{ descendants }}
                    current_cycle={current_cycle}
                    title="Genealoga Completa"
                />
            </RestrictiveGrid>

        </Grid>);
    }
});
