import React from "react";
import { CycleTraveleableProps } from "../lib/CardWithCycleTravel";
import { Grid, Stepper, Step, StepLabel, Hidden, Typography, Tooltip } from "@material-ui/core";
import HierarchyGraph, { ExplanationContainer } from "./HierarchyGraph";
import HierarchyResume from "./HierarchyResume";
import API from "../../../Services/API";

export default function (props: CycleTraveleableProps) {

    const { descendants, descendants_resume } = props.data;

    const [steps, set_steps] = React.useState([] as AsyncReturnType<typeof API["Rangos"]>);

    React.useEffect(() => {
        API.Rangos().then(rangos => set_steps(rangos));
    }, []);

    const rango = descendants_resume?.titulo;
    const selectedStepIndex = steps.findIndex(x => x.id === rango);

    return (<Grid container spacing={2}>
        <Grid item xs={12} >
            <Stepper
                style={{ width: "100%", padding: 0, paddingTop: 0, paddingBottom: 10 }}
                alternativeLabel={true}
                activeStep={selectedStepIndex}
                color="primary">
                {steps.map(x => <Step key={x.id} >
                    <Tooltip title={x.nombre}>
                        <StepLabel>
                            <Typography variant="caption">
                                {x.id}
                            </Typography>
                        </StepLabel>
                    </Tooltip>
                </Step>)}
            </Stepper>
        </Grid>
        <Grid item xs={12} sm={8}>
            <div className="dashboard-card">
                <HierarchyGraph data={descendants} />
            </div>
            <Hidden smUp>
                <Grid item xs={12}>
                    <ExplanationContainer usuarios={descendants} />
                    <hr />
                </Grid>
            </Hidden>
        </Grid>
        <Grid item xs={12} sm={4}>
            <HierarchyResume {...(descendants_resume || {} as any)} />
        </Grid>
        <Hidden xsDown >
            <Grid item xs={12}>
                <ExplanationContainer usuarios={descendants} />
            </Grid>
        </Hidden>
    </Grid>);
}
type AsyncReturnType<T extends (...args: any) => any> =
    T extends (...args: any) => Promise<infer U> ? U :
    T extends (...args: any) => infer U ? U :
    any