import React from "react";
import { GridProps, Grid } from "@material-ui/core";

export default function (props: { show: boolean } & GridProps) {
    var { show, ...remainProps } = props;
    var gridProps = {
        ...remainProps,
        item: true,
        xs: 12
    } as GridProps;

    if (!props.show) return null;

    return (<Grid {...gridProps}>
        {props.children}
    </Grid>);
}