import React from "react";
import { Hidden, Drawer, IconButton, AppBar, Toolbar, Typography, List, ListItem, ListItemIcon, ListItemText, makeStyles } from "@material-ui/core";
import { ShoppingCart, ListAlt, Menu, Close, Help, Inbox, Report, Category, Policy, PeopleAlt, Person, AccountTree } from "@material-ui/icons";
import SettingsIcon from "@material-ui/icons/Settings";
import { Link } from "react-router-dom";
import { useMemo } from "react";
import API from "../../Services/API";

const OPCIONES = [
    { label: "Inicio", Icon: Inbox, path: "/feed" }
    /** NINGUN PATH puede comenzar por order porque dana algunas partes de la logica */
    , { label: "Nuevo Pedido", Icon: ShoppingCart, path: "/order" }

    , { label: "Pedidos", Icon: ListAlt, path: "/my-orders", roles: [1, 2] }
    , { label: "Pedidos", Icon: ListAlt, path: "/queue", roles: [3] }

    , { label: "Politicas", Icon: Policy, path: "/policy" }

    , { label: "Catlogo", Icon: Category, path: "/products", roles: [3] }
    , { label: "Usuarios", Icon: PeopleAlt, path: "/users", roles: [3] }

    , { label: "Reportes", Icon: Report, path: "/reports", roles: [3] }
    , { label: "Ciclos", Icon: AccountTree, path: "/multi-level-cycle", roles: [3] }
    , { label: "Perfil", Icon: Person, path: "/my-profile" }
    , { label: "Configuracin", Icon: SettingsIcon, path: "/settings", roles: [3] }
];

const drawerWidth = 200;

const useStyles = makeStyles(theme => ({
    drawer: {
        [theme.breakpoints.up('sm')]: {
            width: drawerWidth,
            flexShrink: 0
        }
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: drawerWidth,
        background: theme.palette.primary.light,
        color: theme.palette.primary.contrastText,
    },
    icons: {
        color: theme.palette.primary.contrastText
    },
    appBar: {
        display: "block"
    }
}));

export default function (props: {
    username: string,
    ParentName: string,
    rangoHonorifico?: string;
    selectedPath: string,
    rol: number,
    window?: any
}) {

    const container = props.window ? props.window().document.body : undefined;

    const { username, ParentName, rangoHonorifico } = props;

    const selected = props.selectedPath;
    const [isMenuOpen, set_isMenuOpen] = React.useState(false);
    const [Rango, set_Rango] = React.useState({ id: "-1", nombre: "" });

    const classes = useStyles();

    React.useEffect(() => {

        if (Rango.id !== props.rangoHonorifico)
            API.Rangos().then(rangos => {
                const found = rangos.find(x => props.rangoHonorifico! === x.id);
                if (!found) return;
                set_Rango(found);
            }).catch(ex => set_Rango({
                id: "-1", nombre: "Rango no encontrado"
            }));

    }, [props.rangoHonorifico]);

    const accesos = useMemo(() => {
        return OPCIONES.filter(x => (x.roles || [props.rol]).indexOf(props.rol) >= 0);
    }, [props.rol]);

    const drawerChildren = (<div style={{ display: "flex", flexDirection: "column" }}>
        <div style={{ textAlign: "center", marginTop: 10, marginBottom: 10 }}>
            <Typography style={{ display: "block" }} variant="button">{username}</Typography>
            <Typography style={{ display: "block" }} variant="caption">PATROCINADOR:</Typography>
            <Typography style={{ display: "block" }} variant="caption">{ParentName}</Typography>
            <Typography style={{ display: "block" }} variant="button">{Rango.nombre}</Typography>
        </div>
        <List style={{ flex: 1 }}>
            {accesos.map(o => (<ListItem onClick={() => set_isMenuOpen(false)} selected={selected === o.path} component={Link} to={o.path} button key={o.label}>
                <ListItemIcon >{<o.Icon className={classes.icons} />}</ListItemIcon>
                <ListItemText primaryTypographyProps={{
                    variant: "button",
                    style: {
                        fontWeight: selected === o.path ? "bolder" : "normal"
                    }
                }} primary={o.label} />
            </ListItem>))}
        </List>
        <ListItem className="a-link" component="a" target="_a" href="https://wa.me/" >
            <ListItemIcon> <Help className={classes.icons} /> </ListItemIcon>
            <ListItemText primaryTypographyProps={{ variant: "button" }} primary="AYUDA" />
        </ListItem>
    </div >);

    return (<>
        <nav className={classes.drawer}>
            <Hidden smUp implementation="css" >
                <Drawer
                    container={container}
                    variant="temporary"
                    anchor={'left'}
                    open={isMenuOpen}
                    onClose={() => set_isMenuOpen(false)}
                    classes={{ paper: classes.drawerPaper }}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    <div className={classes.toolbar} style={{ textAlign: "right" }}>
                        <IconButton onClick={() => set_isMenuOpen(false)}>
                            <Close className={classes.icons} />
                        </IconButton>
                    </div>
                    {drawerChildren}
                </Drawer>
            </Hidden>
            <Hidden xsDown implementation="css">
                <Drawer
                    classes={{ paper: classes.drawerPaper }}
                    variant="permanent"
                    open
                >
                    {drawerChildren}
                </Drawer>
            </Hidden>
        </nav>

        <Hidden smUp implementation="css" >
            <AppBar position="fixed">
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={() => set_isMenuOpen(true)}
                    >
                        <Menu />
                    </IconButton>
                    <Typography style={{ flexGrow: 1 }}>{(OPCIONES.find(x => x.path === selected) || {}).label}</Typography>
                </Toolbar>
            </AppBar>
        </Hidden>
    </>)
}