import React from 'react';
import { Typography, CssBaseline, withStyles, Hidden } from '@material-ui/core';
import { Switch, Route, Redirect, HashRouter } from "react-router-dom";
import Order from './../Components/Order';
import OrderList from './../Components/OrderList';
import Feed from './../Components/Feed';
import Reports from './../Components/Reports';
import OrderQueue from '../Components/OrderQueue';
import Products from '../Components/Products';
import Users from '../Components/Users';
import PolicyPage from '../Components/PolicyPage';
import TermsAndConds from './TermsAndConds';
import UserProfile from '../Components/UserProfile';
import { Provider } from 'react-redux';
import { store } from '../Components/Order/Redux/Store';
import Settings from '../Components/Settings';
import MultiLevel from '../Components/MultiLevel';
import NavigationDrawer from '../Components/NavigationDrawer';
const drawerWidth = 200;

export default withStyles(theme => ({
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    [theme.breakpoints.up('sm')]: {
      marginLeft: drawerWidth
    }
  }
}))(class Main extends React.Component<MainProps, any> {

  state = {
    selected: ""
    , editarPVPsdurantePedido: false
    , username: ""
    , userId: undefined as number
    , ParentName: ""
    , rangoHonorifico: ""
    , token: ""
    , rol: 0
    , descuentos: [] as Descuento[]
    , mustAcceptTermsAndConds: false
    , solicitarDatosAlIniciar: false
  };

  constructor(props: any) {
    super(props);
    this.onMounted = this.onMounted.bind(this);
    this.TermsAndCondsAccepted = this.TermsAndCondsAccepted.bind(this);
    this.state.username = props.username;
    this.state.ParentName = props.ParentName;
    this.state.rangoHonorifico = props.rangoHonorifico;
    this.state.rol = props.rol;
    this.state.descuentos = props.descuentos || [];
    this.state.editarPVPsdurantePedido = props.editarPVPsdurantePedido;
    this.state.mustAcceptTermsAndConds = props.mustAcceptTermsAndConds;
    this.state.solicitarDatosAlIniciar = props.solicitarDatosAlIniciar;
    this.state.userId = props.id;
  }
  onMounted(selected: string) {
    this.setState({ selected });
  }
  handleMenu() {
    this.setState(prev => {
      const state = { ...prev };
      state.isMenuOpen = !state.isMenuOpen;
      return state;
    });
  }
  TermsAndCondsAccepted() {
    this.setState({ mustAcceptTermsAndConds: false });
  }
  render() {
    const { mustAcceptTermsAndConds, solicitarDatosAlIniciar } = this.state;
    const { window, classes } = this.props;

    if (mustAcceptTermsAndConds)
      return (<TermsAndConds onDone={this.TermsAndCondsAccepted} />);

    if (solicitarDatosAlIniciar)
      return (<div style={{ textAlign: "center", paddingTop: 15 }}>
        <Typography variant="overline"  >Hola! estamos actualizando la informacin de nuestros miembros. Por favor llena los campos que faltan para continuar.</Typography>
        <UserProfile userId={this.state.userId} onSubmit={() => this.setState({ solicitarDatosAlIniciar: false })} />
      </div>);


    return (<HashRouter basename="/app" >
      <div style={{ width: "100%", height: "100%", display: "flex", flexDirection: "column" }}>
        <CssBaseline />
        <NavigationDrawer
          ParentName={this.state.ParentName}
          rol={this.state.rol}
          selectedPath={this.state.selected}
          username={this.state.username}
          rangoHonorifico={this.state.rangoHonorifico}
          window={this.props.window}
        />
        <main className={classes.content}>
          <Provider store={store}>
            <Hidden smUp implementation="css">
              <div className={classes.toolbar}></div>
            </Hidden>
            {process.env.NODE_ENV === "development" ? <Typography style={{ color: "blue", background: "yellow", textAlign: "center" }} variant="h6" > AMBIENTE DE PRUEBAS </Typography> : null}
            <Switch>
              <Route path="/feed" render={props => <Feed {...props} onMounted={this.onMounted} username={this.state.username} rol={this.state.rol} />} />
              <Route path="/order" render={_ => <Order onMounted={this.onMounted} rol={this.state.rol} descuentos={this.state.descuentos} editablePrices={this.state.editarPVPsdurantePedido} />} />
              <Route path="/my-orders" render={props => <OrderList {...props} rol={this.state.rol} onMounted={this.onMounted} />} />
              <Route path="/queue" render={props => <OrderQueue {...props} onMounted={this.onMounted} />} />

              <Route path="/policy" render={props => <PolicyPage
                onMounted={this.onMounted}
                path={props.match && props.match.path}
                TextSrc="https://docs.google.com/document/d/e/2PACX-1vSC-0iC8dbpYgrNhlDyRWAQdwdFM4esg40JLZQS-zg6Koix_w1B86KoGI3YPpq-TdNjoJV0eenbcRKv/pub?embedded=true"
              />}
              />

              <Route path="/users" render={props => <Users {...props} onMounted={this.onMounted} />} />
              <Route path="/products" render={props => <Products {...props} onMounted={this.onMounted} />} />

              <Route path="/reports" render={props => <Reports {...props} rol={this.state.rol} onMounted={this.onMounted} />} />
              <Route path="/my-profile" render={props => <UserProfile {...props} userId={this.state.userId} onMounted={this.onMounted} />} />
              <Route path="/settings" render={props => <Settings {...props} onMounted={this.onMounted} />} />
              <Route path="/multi-level-cycle" render={props => <MultiLevel {...props} onMounted={this.onMounted} />} />
              <Route path="/" exact><Redirect to="/feed" /></Route>
            </Switch>
          </Provider>
        </main>
      </div>
    </HashRouter>)
  }
});

interface MainProps {
  username: string;
  rol: number;
  ParentName?: string;
  editarPVPsdurantePedido?: boolean;

  token: string;
  window?: any;
  classes: any;
}